import React from 'react';
import './Input.css';

function Input( { type, placeholder, customClass, value, inputHandler } ){
  return(
      <div>
        <input 
          type={ type } 
          placeholder={ placeholder } 
          className={ customClass } 
          value={ value } 
          onChange={ inputHandler }
          name={ customClass }/>
      </div>
    )
}

export default Input;